"use strict";

require('../sass/fate.sass');

import {HoroModel, PlanetModel, ElementModel, LineColors, QuadModel} from '../components/utils/common';
import fateTmpl from '../tmpl/utils/fate-render.pug';
import initBridge from '../components/utils/bridge';

window.HoroModel = HoroModel;
window.PlanetModel = PlanetModel;
window.ElementModel = ElementModel;
window.QuadModel = QuadModel;

document.body.addEventListener('touchstart',  () => null);

// 角度转弧度
const degToRadian = (deg) => {
    return Math.PI * deg / 180;
};

// 颜色格式转换
const hexToRgb = function(hex,opacity) {

    let rgb = [];
    hex = hex.substr(1);//去除前缀 # 号

    if (hex.length === 3) { // 处理 "#abc" 成 "#aabbcc"
        hex = hex.replace(/(.)/g, '$1$1');
    }

    hex.replace(/../g, function(color){
        rgb.push(parseInt(color, 0x10));//按16进制将字符串转换为数字
    });
    return "rgba(" + rgb.join(",") + ","+opacity + ')';
};

// 画星球
const drawHouseSvg = (data) => {

    let list = data.NatalChart.MainPlanetList;
    let lines = data.NatalChart.AspectList;
    // let lines = [{s:2,t:6,id:1},{s:3,t:9,id:2}];

    let wrap = document.querySelector('.planet-icons');

    // 圆的半径 raduis
    let width  = parseFloat(getComputedStyle(wrap).width);
    // let width  = parseInt(getComputedStyle(wrap).width);

    let radius = width / 2;

    // 角度->弧度
    let radian = Math.PI / 180;

    /*
     * 计算并确定各个子菜单的最终位置
     */
    let icons = document.querySelectorAll('.planet-icons .icon');

    let lineCanvas    = document.getElementById('line-canvas');
    let lineCtx       = lineCanvas.getContext("2d");
    let lineRadius    = lineCanvas.height/2 - 2;

    lineCtx.beginPath();
    lineCtx.strokeStyle = 'rgba(255,255,255,.1)';
    lineCtx.lineWidth   = 2;
    lineCtx.arc(lineCanvas.width/2, lineCanvas.height/2, lineRadius, 0, 2*Math.PI,  true);  // 逆时针
    lineCtx.stroke();

    for(var p in list){

        let d = list[p].DegreeUT + data.NatalChart.DegOffset + 90;

        let myAngle = d * radian ;  // θ
        let myX = radius + radius * Math.sin( myAngle ); // x=r+rcos(θ)
        let myY = radius + radius * Math.cos( myAngle ); // y=r+rsin(θ)

        icons[p].style.left = myX + 'px';
        icons[p].style.top  = myY + 'px';
        icons[p].style.opacity  = 1;

        let myLX = lineRadius + lineRadius * Math.sin( myAngle ); // x=r+rcos(θ)
        let myLY = lineRadius + lineRadius * Math.cos( myAngle ); // y=r+rsin(θ)


        if(list[p].ID == 23 || list[p].ID == 24){

            lineCtx.beginPath();
            lineCtx.strokeStyle = 'rgba(255,255,255,.5)';
            lineCtx.lineWidth   = 1;

            let myELX = lineRadius + lineRadius * Math.sin( myAngle + Math.PI ); // x=r+rcos(θ)
            let myELY = lineRadius + lineRadius * Math.cos( myAngle + Math.PI); // y=r+rsin(θ)

            lineCtx.moveTo(parseFloat(myLX),parseFloat(myLY));
            lineCtx.lineTo(parseFloat(myELX) + 2,parseFloat(myELY) + 2);
            lineCtx.stroke();
        }

        icons[p].setAttribute('data-draw',myLX+'&'+myLY);
        // icons[p].setAttribute('id','point_'+list[p].ID);
    }

    for(var l in lines){

        let id = lines[l].ID;
        let pointS = document.querySelector("#point_"+lines[l].PX);
        let pointE = document.querySelector("#point_"+lines[l].PY);
        let color  = LineColors[id];

        let numS = pointS.getAttribute('data-draw').split('&');
        let numE = pointE.getAttribute('data-draw').split('&');

        lineCtx.beginPath();
        lineCtx.strokeStyle = color;
        lineCtx.lineWidth   = 1.5;

        lineCtx.moveTo(parseFloat(numS[0]),parseFloat(numS[1]));
        lineCtx.lineTo(parseFloat(numE[0]),parseFloat(numE[1]));
        lineCtx.stroke();
    }
    // while (n < total){

    //     // 当前子菜单与x轴正向的夹角 θ （角度->弧度）  移到星座中心
    //     let myAngle = (startAngle + gap * n + 15) * radian;  // θ

    //     // 计算当前子菜单相对于左上角(0,0)的坐标 (x,y)
    //     let myX = radius + radius * Math.cos( myAngle ); // x=r+rcos(θ)
    //     let myY = radius + radius * Math.sin( myAngle ); // y=r+rsin(θ)

    //     icons[n].style.left = myX + 'px';
    //     icons[n].style.top  = myY + 'px';
    //     n++;
    // }
};

const drawPath = (data) => {

    let canvas    = document.getElementById('s-canvas');
    let ctx       = canvas.getContext("2d");
    let w         = canvas.width;
    let h         = canvas.height;
    let line      = 4;
    let pointX    = w / 2;
    let pointY    = h / 2;
    let radius    = pointX - 6;  // 半径 需要减去一点，否者会溢出边界
    let opacity   = 0.4;

    let step   = 0.5;   //间隔数值
    let len    = 0;
    let total  = 360;
    let single =  - total/12;


    let circleWidth = 50;  //圆环宽度
    let circleBegin = 30; // 圆环距离外层border 距离
    let houseBegin  = 80;  // 十二宫距离外层boder

    let svgWrap = document.querySelector('.icons');
    let svgIcons = document.querySelectorAll('.icons .icon');
    let svgIconRadius = parseFloat(getComputedStyle(svgWrap).width)/2;

    // 旋转度数
    let offset = data.NatalChart.DegOffset + 120;
    let circleWrap = document.querySelector('.canvas .circle');

    let housesList = data.NatalChart.HousesList;

    while(len < 12){

        let start = single*len - step;
        let end   = single*(len + 1); // 结束是正值 则正转否者倒转

        // 画圆环边框
        ctx.beginPath();
        ctx.lineWidth   = line;
        ctx.fillStyle = "#9ea7b8";
        ctx.strokeStyle = hexToRgb(HoroModel[len].color,1);
        ctx.arc(pointX, pointY, radius, degToRadian(start), degToRadian(end),  true);  // 逆时针
        ctx.stroke();

        // 画圆环内容
        ctx.beginPath();
        ctx.lineWidth   = circleWidth;
        ctx.strokeStyle = hexToRgb(HoroModel[len].color,opacity);
        ctx.arc(pointX, pointY, radius - circleBegin, degToRadian(start), degToRadian(end),  true);  // 逆时针
        ctx.stroke();

        // 画星座svg

        // 当前子菜单与x轴正向的夹角 θ （角度->弧度）  移到星座中心
        let myAngle = degToRadian(0 + 30 * len + 15);  // θ
        let myX = svgIconRadius + svgIconRadius * Math.cos( myAngle ); // x=r+rcos(θ)
        let myY = svgIconRadius + svgIconRadius * Math.sin( myAngle ); // y=r+rsin(θ)
        svgIcons[len].style.left = myX + 'px';
        svgIcons[len].style.top  = myY + 'px';
        svgIcons[len].style.transform  = 'translate3d(-50%,-50%,0) rotate(-'+offset+'deg)';
        svgIcons[len].style.WebkitTransform  = 'translate3d(-50%,-50%,0) rotate(-'+offset+'deg)';
        svgIcons[len].style.opacity  = 1;


        len++;
    }

    // 旋转角度
    circleWrap.setAttribute('style','transform: rotate('+offset+'deg);-webkit-transform: rotate('+offset+'deg);');

    // 画十二宫
    let hCanvas    = document.getElementById('s-house');
    let hCtx       = hCanvas.getContext("2d");
    let hRadius    = radius - houseBegin;
    let houseBeginDeg = 180;

    hCtx.lineWidth   = circleWidth;
    hCtx.strokeStyle = 'rgba(255,255,255,0.05)';

    // 角度->弧度
    let radian = Math.PI / 180;



    for(let h in housesList){

        let id = housesList[h].ID;
        let next = h*1+1;

        if(next == housesList.length){ next = 0; }

        let tempDeg = housesList[next].DegreeUT - housesList[h].DegreeUT;

        let houseEndDeg = houseBeginDeg - tempDeg;

        if(tempDeg < 0){
            tempDeg = housesList[next].DegreeUT + 360 -housesList[h].DegreeUT;
        }

        let centerDeg = houseBeginDeg - tempDeg/2;

        hCtx.beginPath();
        hCtx.arc(hCanvas.width/2, hCanvas.height/2, hRadius, degToRadian(houseBeginDeg - 1), degToRadian(houseEndDeg),  true);  // 逆时针
        hCtx.stroke();

        houseBeginDeg = houseEndDeg;


        // 当前子菜单与x轴正向的夹角 θ （角度->弧度）  移到星座中心

        hCtx.beginPath();
        hCtx.fillStyle="rgba(255,255,255,.4)";
        hCtx.font=" 20px Helvetica";

        // console.log(houseEndDeg +centerDeg);
        // let myHAngle = houseEndDeg * radian;  // θ
        let myHAngle = centerDeg * radian;  // θ

        let myX = hCanvas.width/2 +  hRadius * Math.cos( myHAngle ) - 5; // x=r+rcos(θ)
        let myY = hCanvas.height/2 +  hRadius * Math.sin( myHAngle ) + 5; // y=r+rsin(θ)
        hCtx.fillText(id,myX,myY);
        hCtx.stroke();
    }

    // hCtx.beginPath();
    // hCtx.strokeStyle = 'rgba(255,255,255,.1)';
    // hCtx.lineWidth   = 2;
    // hCtx.arc(hCanvas.width/2, hCanvas.height/2, hRadius - houseBegin, 0, 2*Math.PI,  true);  // 逆时针
    // hCtx.stroke();

    // 画星球
    drawHouseSvg(data);
};

// 初始化页面
const initPage = (data) => {

    document.querySelector('.fate-page').innerHTML = fateTmpl(data);

    let welcome     = document.querySelector('.welcome');
    let chart       = document.querySelector('.chart');
    let chartCanvas = document.querySelector('.chart .canvas');
    let chartBtn    = document.querySelector('.chart .btn');
    let result     = document.querySelector('.result');

    document.querySelector('.begin').addEventListener('click',function() {

        welcome.style.opacity = 0;
        window.scroll(0,0);
        setTimeout(function(){
            welcome.classList.add('fn-hide');
            chart.classList.remove('fn-hide');

            setTimeout(function(){

                chartCanvas.classList.remove('fn-hide');
                drawPath(data);

                setTimeout(function(){

                    chartBtn.style.opacity = 1;
                },2000);

            },500);

        },500);

    },false);

    chartBtn.addEventListener('click',function() {
        chart.style.opacity = 0;
        result.classList.remove('fn-hide');
        setTimeout(function(){
            chart.classList.add('fn-hide');
            result.style.opacity = 1;
        },500);
    },false);

    // document.querySelector('.begin').addEventListener('click',function(e) {

    // },false);

};

initBridge(function(bridge){
    bridge.registerHandler('getHoroChart', function(data,responseCallback){
        initPage(JSON.parse(data));
        responseCallback(true);
    });

    bridge.registerHandler('sharePrepare', function(data,responseCallback){

        let result = document.querySelector('.result');

        result.classList.add('result-share');

        bridge.callHandler('shareReady', function(data,response) {
                result.classList.remove('result-share');
                return response(true);
        });
        responseCallback(true);
    });
});

